.popup {
    box-sizing: border-box;

    background-color: rgba(0, 0, 0, 0.8);

    position: fixed;

    height: 100%;
    width: 100%;
    left: 0;
    top: 0;

    display: flex;
    align-items: center;
    justify-content: center;

    visibility: hidden;
    opacity: 0;
    transition: opacity 0.6s, visibility 0s 0.6s;
}